/*
Bot turned styles
 */

$click-animation-time: 1s;
$blink-animation-time: 5s;

.animation-container {
  position: absolute;

  top: 80px;
  left: -198px;

  @media (max-width: 767px) {
    top: 0;
    left: 0;
  }

  &:not(.stop-animation) {
    .bot_turned {
      width: 180px;
      height: 180px;

      cursor: pointer;

      @media (max-width: 767px) {
        width: 100px;
        height: 100px;
      }

      #eyes {
        animation: eyes-blink-bot-turned $blink-animation-time infinite;
      }

      #mouth {
        animation: mouth-close-open-bot-turned $blink-animation-time infinite;
      }

      #ear-left {
        animation: ear-left-move-bot-turned $blink-animation-time infinite;
      }

      #ear-right {
        animation: ear-right-move-bot-turned $blink-animation-time infinite;
      }

      #forhead {
        animation: forhead-blink-bot-turned $blink-animation-time infinite;
      }

      #forhead-scale {
        animation: forhead-scale-blink-bot-turned $blink-animation-time infinite;
      }
    }

    &.click-animation {
      pointer-events: none;

      .bot_turned {
        #eye-left {
          animation: eye-left-bot-turned_clicked $click-animation-time;
        }

        #eye-right {
          animation: eye-right-bot-turned_clicked $click-animation-time;
        }

        #mouth {
          animation: mouth-bot-turned_clicked $click-animation-time;
        }

        #ear-right {
          animation: ear-right-bot-turned_clicked $click-animation-time;
        }

        #ear-left {
          animation: ear-left-bot-turned_clicked $click-animation-time;
        }

        #chin {
          animation: chin-rotate-bot-turned_clicked $click-animation-time;
        }

        // Need to stop main animation

        #eyes {
          animation: eyes-buffer $click-animation-time;
        }

        #forhead {
          animation: forhead-buffer $click-animation-time;
        }

        #forhead-scale {
          animation: forhead-scale-buffer $click-animation-time;
        }
      }
    }

    &.turned {
      animation: bot-up-down 3s ease-in-out infinite;

      &.click-animation {
        .bot {
          animation: bot-turned-rotation_clicked $click-animation-time;
        }
      }
    }
  }
}

@keyframes bot-up-down {
  0% {
    transform: translateY(0px);
    transition-property: transform;
    transition-timing-function: cubic-bezier(0.57, 0.01, 0.69, 0.82);
  }
  50% {
    transform: translateY(10px);
    transition-timing-function: ease-in;
  }
  100% {
    transform: translateY(0px);
    transition-timing-function: ease-in;
  }
}

@keyframes eyes-blink-bot-turned {
  0% {
    transform: translate(90.984131px, 85.22558px) scale(1, 1);
  }
  90% {
    transform: translate(90.984131px, 85.22558px) scale(1, 1);
    animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  91% {
    transform: translate(90.984131px, 85.22558px) scale(1, 1.04);
    animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  94.800000% {
    transform: translate(90.984131px, 85.22558px) scale(1, 0.15);
    animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  99% {
    transform: translate(90.984131px, 85.22558px) scale(1, 1.04);
    animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  100% {
    transform: translate(90.984131px, 85.22558px) scale(1, 1);
  }
}

