  }
  94.800000% {
    transform: translate(43.375011px, 75.049042px) rotate(-1deg);
  }
  99.200000% {
    transform: translate(43.375011px, 75.049042px) rotate(0deg);
  }
  100% {
    transform: translate(43.375011px, 75.049042px) rotate(0deg);
