.wrap {
  display: flex;
  width: 100%;
  margin-top: 46px;
  position: relative;
}

.limiter {
  overflow: hidden;
  width: 100%;
  height: 100%;
  max-width: 696px;
  min-height: 210px;
  margin: 0 auto;
  position: relative;
  display: flex;
  justify-content: flex-start;
}

.carousel {
  display: flex;
  flex-shrink: 1;
  height: 100%;
  width: 100%;
  transition: transform 0.3s;
  padding: 24px 12px;
  -ms-overflow-style: none;
  scrollbar-width: none;
  will-change: transform;
}

.carousel::-webkit-scrollbar,
.carousel::-webkit-scrollbar {
  display: none;
}

.carousel > * {
  flex-shrink: 0;
}

.carousel.show-3 > * {
  width: calc(100% / 3);
  flex-basis: calc(100% / 3);
}

.left-arrow,
.right-arrow {
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
}

.left-arrow {
  left: 14px;
}

.right-arrow {
  right: 14px;
  transform: scaleX(-1) translateY(-50%);
}
